.dnd {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;

  color: var(--base-dark);
  background: -webkit-gradient(linear, left top, left bottom, from(var(--base)), to(var(--base-light)));
  background: -webkit-linear-gradient(top, var(--base) 0%, var(--base-light) 100%);
  background: -o-linear-gradient(top, var(--base) 0%, var(--base-light) 100%);
  background: linear-gradient(to bottom, var(--base) 0%, var(--base-light) 100%);
  height: 100%;
  width: 100%;
  padding: 40px 0 0 0;
}

.dnd h4 {
  margin-bottom: 10px;
  text-align: center;
  color: var(--black);
}

.dnd-column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.dnd-title {
  margin: 10px 25px;
  text-align: center;
  color: var(--black);
}

pre {
  padding: 4px;
  font-size: 12px;
  background-color: var(--base);
  border: 1px solid var(--base-dark);
}



