.listitem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 40px;
  font-size: 14px;
  line-height: 40px;

  color: var(--base);
  background-color: var(--base-dark);
  border: 1px solid var(--base);

  cursor: move;
}

.listitem-over {
  background-color: var(--black);
}



